import Emoji from '../objects/Emoji';
import Score from '../objects/Score';
import Clock from '../objects/Clock';


var TILE = [1188, 971, 1075, 797, 1081, 1082, 678, 643, 1407, 1405, 1675, 826, 492, 1287, 1115,756,670,794,708,665,622,536,321,498,455,240,197,154,112,68,84,204];
var TILENAME = [];

// Mixed colour items
TILENAME[68] = 'Cat';
TILENAME[498] = 'Monkey';
TILENAME[670] = 'Pig';

// Blue items
TILENAME[1081] = 'Bird';
TILENAME[1188] = 'Footprint';
TILENAME[197] = 'Whale';
TILENAME[204] = 'Book';

// White items
TILENAME[84] = 'Snowman';
TILENAME[112] = 'Panda';
TILENAME[1115] = 'Unicorn';
TILENAME[455] = 'Sheep';
TILENAME[1082] = 'Penguin';

// Grey items
TILENAME[756] = 'Elephant';
TILENAME[1287] = 'Shark';
TILENAME[1407] = 'Bell';
TILENAME[643] = 'Skull';

// Red items
TILENAME[321] = 'Mushroom';
TILENAME[536] = 'Melon';
TILENAME[1675] = 'Heart';
TILENAME[1075] = 'Fish';

// Yellow items
TILENAME[622] = 'Lemon';
TILENAME[665] = 'Banana';
TILENAME[708] = 'Pineapple';
TILENAME[492] = 'Star';
TILENAME[1405] = 'Padlock';
TILENAME[797] = 'Trophy';
TILENAME[678] = 'Fire';
TILENAME[971] = 'Bee';


// Green items
TILENAME[154] = 'Crocodile';
TILENAME[794] = 'Apple';
TILENAME[240] = 'Snail';
TILENAME[826] = 'Tree';


export default class Game extends Phaser.State {
  create() {
    this.stage.backgroundColor = '#1E70A2'; //'#2d2d2d';

    // Allow screen to shake on error
    //this.setupScreenQuake();
  
    this.strobe = this.game.add.tween(this.stage).to({backgroundColor: 0xFFFFFF}, 20, Phaser.Easing.Linear.None, false, 0, -1, true);

    // Sounds for the state
    this.sndGood = this.add.audio('choice-good');
    this.sndBad = this.add.audio('choice-bad');
    this.sndRotate = this.add.audio('level-rotate');
    this.sndNewBoard = this.add.audio('level-newboard');

    // Create a group for the user info
    this.hud = this.add.group();

    // Display message
    var style = { font: '72px Arial', fill: '#fff', align: 'center'};
    this.findRequest = new Phaser.Text(this.game, this.world.centerX, this.world.height, 'Eyes down for hunting', style);
    this.findRequest.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
    //this.findRequest.setShadow(3, 3, 'rgba(0,0,0,0.5)', 2);
    this.findRequest.stroke = '#000000';
    this.findRequest.strokeThickness = 20;
    this.findRequest.anchor.set(0.5, 1);
    this.findRequest.resolution = 1; // does this do anything?
    this.hud.add(this.findRequest);

    // Show a clue to what needs to be found next
    // var t = new Emoji(this.game, this.game.world.centerX, this.findRequest.y, 25, 1082, false);
    // t.anchor.set(0.5, 1.5);
    // this.findTile = this.add.existing(t);
    // @todo adding this on screen slows iphone fps down crazy - why?!

    // Initalise Score
    this.score = new Score(this.game, 0);
    this.hud.add(this.score);

    // Setup countdown timer
    this.clock = new Clock(this.game, 30);
    this.clock.alarmSignal.add(this.gameOver, this);
    this.hud.add(this.clock);

    // Starting Level
    this.game.level = 0;

    // Normal coloured tiles to start with
    this.tintmode = 0xffffff;

    // Create an initial board of tiles
    this.emojiTiles = this.add.group();
    this.generatePile();

    this.nextToFind();
    this.levelUp();

    this.info = this.add.text(16, 36, ' ');
    this.info.font = "Courier";
    this.info.fontSize = 14;
    this.info.fill = "#ffffff";
    this.info.lineSpacing = 4;
    this.info.setShadow(2, 2);
    //this.info.scale.setTo(this.scaleRatio/2, this.scaleRatio/2);

    this.game.world.bringToTop(this.hud);
  }

  render() {
    //this.emojiTiles.forEach(this.debugBounds, this, false);
  }

  update() {
    //this.scaleinfo();
  }

  debugBounds(sp) {
    this.game.debug.spriteBounds(sp);
  }
  
  nextToFind() {
    // Pick something random from the current tile pile whilst avoiding the same item as last time
    var newFindRequest = this.currentFindRequest;
    while(this.currentFindRequest == newFindRequest) {
      newFindRequest = Phaser.ArrayUtils.getRandomItem(this.placedTiles).tile;
    }

    this.currentFindRequest = newFindRequest;

    // Update the picker message
    //this.findTile.setFrame(this.currentFindRequest);
    this.findRequest.text = 'Find a ' + TILENAME[this.currentFindRequest];
  }

  clicked(sprite) {
    sprite.tapped();

    if(this.currentFindRequest == sprite.name) {
      this.sndGood.play();

      //@todo emitter https://phaser.io/examples/v2/particles/click-burst
      // https://phaser.io/examples/v2/particlestorm/confetti

      // Change the pile on every click/tap
      if (this.game.level > 2) {
        this.generatePile();
      }

      this.nextToFind();
    
      this.clock.addTime(1); // free time bonus

      var points = 15; //Math.floor(sprite.height * 0.08);
      this.score.addPoints(points);

      this.levelUp(this.score.getScore());
    } else {
      // Shake screen for incorrect tap
      this.sndBad.play();
      this.camera.x = 0;
      this.add.tween(this.camera).to({x: this.camera.x - 50}, 50, Phaser.Easing.Bounce.InOut, true, 0, 4, true).start();
      this.score.addPoints(-5);
    }
  }

  generatePile() {
    this.placedTiles = [];
    var sample = {};
    this.tilePile = Phaser.ArrayUtils.shuffle(TILE).slice(2, 22); // Attempt 20 tiles on the board

    // Define an area that tiles must fit on
    this.board = new Phaser.Ellipse(0, 0, this.world.width, this.world.height);

    // Draw the board parimeter
    // var graphics = this.add.graphics(0, 0); // @debug
    // graphics.lineStyle(10, 0x00ff00, 1); // @debug
    // graphics.drawEllipse(this.board.x + (this.board.width/2),
    //   this.board.y + (this.board.height/2) ,
    //   this.board.width/2,
    //   this.board.height/2); // @debug

    // Group tiles for easy manipulation in levels
    this.emojiTiles.removeAll(true, true, false);

    // Set all tiles central pivot point for rotation of board
    this.emojiTiles.pivot.x = this.world.centerX;
    this.emojiTiles.pivot.y = this.world.centerY;
    this.emojiTiles.x = this.board.x + this.board.width/2;
    this.emojiTiles.y = this.board.y + this.board.height/2;

    // Define some common tween animations
    this.rotateBoard = this.game.add.tween(this.emojiTiles).to({rotation: 360}, 800000, Phaser.Easing.Linear.None, false, 0, -1, false);
    this.rotateBoard.onStart.add(this.rotateBoardSound, this);
    this.nudgeBoard = this.game.add.tween(this.emojiTiles).to({rotation: +45}, 1000, Phaser.Easing.Linear.None, false, 0, 0, false);

    // Display our pile of tiles inside the board
    this.stepcount = 1;
    this.tilePile.forEach(
      function(tilez) {
        sample = this.getBestCandidate(250);

        if (sample.radius != 0) {
          sample.tile = tilez;

          // Store new tile location
          this.placedTiles.push(sample);

          // Render tile on screen
          var t = new Emoji(this.game, sample.x, sample.y, sample.radius, tilez, true);
          t.events.onInputDown.add(this.clicked, this);
          this.emojiTiles.add(t);
        }
      },this);

    // Ensure any colour tint persists
    this.emojiTint();

    //console.log('rendered ' + this.placedTiles.length + ' items with step of ' + this.stepcount);
  }

  getBestCandidate(k = 128) {
    var winningSample = new Phaser.Circle();

    // Remember the ellipse size of the full board
    var tr = {w: this.board.width, h: this.board.height, x: this.board.x, y: this.board.y};

    // Pick a radius for the tile
    var baseRadiusMin = 18, baseRadiusMax = this.world.width/4;
    var p = (this.stepcount - 1) / (this.tilePile.length - 1);
    this.stepcount+= 0.5;
    var np = 1 - p;
    var maxRadius = Math.floor(((baseRadiusMax - baseRadiusMin) * np) + baseRadiusMin);
    var margin = this.stepcount * 8;
    var minRadius = (maxRadius - margin) < baseRadiusMin ? baseRadiusMin : maxRadius - margin;

    var r = Phaser.Math.between(minRadius, maxRadius);// * this.game.scaleRatio;
    var candidate = new Phaser.Circle(0, 0, r * 2);

    // Reduce board size to avoid a tile hanging outside of board
    this.board.height = tr.h - r;
    this.board.width = tr.w - r;
    this.board.x = this.board.x + (r/2);
    this.board.y = this.board.y + (r/2);

    // var graphics = this.add.graphics(0, 0); // @debug
    // graphics.lineStyle(3, 0xff0000, 0.7); // @debug
    // graphics.drawEllipse(this.board.x + (this.board.width/2),
    //   this.board.y + (this.board.height/2),
    //   this.board.width/2,
    //   this.board.height/2); // @debug

    for(var i = 0; i < k; i++){

      // Find a point within the defined circular board area
      this.erandom(candidate, this.board.x, this.board.y, this.board.width, this.board.height);

      // @Debug show attempted candidates
      // var graphics = this.add.graphics(0, 0); // @debug
      // graphics.lineStyle(1, 0xff0000, 0.3); // @debug
      // graphics.drawCircle(candidate.x, candidate.y, candidate.diameter);

      // First candidate then it's the best one!
      if(this.placedTiles.length == 0) {
        winningSample = candidate;
        break;
      }

      // Check if the candidate overlaps any existing tiles
      var clashed = false;
      clashed = this.clashTest(candidate);
      if(!clashed) {
        winningSample = candidate;
        break;
      }
    }

    // Reset game board radius for next round
    this.board.height = tr.h;
    this.board.width = tr.w;
    this.board.x = tr.x;
    this.board.y = tr.y;

    return winningSample;
  }

  erandom(out, x, y, width, height) {

    if (out === undefined) { out = new Phaser.Point(); }

    var p = Math.random() * Math.PI * 2;
    var r = Math.random();

    out.x = Math.sqrt(r) * Math.cos(p);
    out.y = Math.sqrt(r) * Math.sin(p);

    out.x = x + width/2 + (out.x * width / 2.0);
    out.y = y + height/2 + (out.y * height / 2.0);

    return out;
  }

  clashTest(tileCandidate) {
    var otherSample;
    var clash = false;

    for(var i = 0; i < this.placedTiles.length; i++) {
      otherSample = this.placedTiles[i];
      //otherSample.radius += 2; // Add a margin, needs to work on a COPY of circle

      if(Phaser.Circle.intersects(tileCandidate, otherSample)) {
        clash = true;
        return clash;
      }
    }

    return false;
  }

  levelUp(currentScore) {
    var level = Math.floor(currentScore/100)+1;

    // Do we level up?
    if(level != this.game.level) {
      this.game.level = level;
      this.sndNewBoard.play();

      switch(this.game.level) {
      case 1:
        //nothing
        break;

      case 2:
        this.emojiSpin();
        //this.nudgeBoard.start();
        break;
  
      case 3:
        // Throb
        //this.tween = this.game.add.tween(this.scale).to({x: this.scale.x/1.5, y: this.scale.y/1.5}, 500, Phaser.Easing.Sinusoidal.Out, true, 0, -1, true); // @todo this reset the sprite scale :(
        this.emojiTint(0xff0505); // red
        this.emojiSpin(false);
        //this.nudgeBoard.start();
        break;
  
      case 4:
        this.generatePile();
        this.nextToFind();
        
        this.rotateBoard.start();
        break;

      case 5:
        
        this.emojiTiles.callAll('spinEmoji');
        this.emojiTint(0x00dd00); // green
        break;

      case 7:
        //this.generatePile();
        this.nextToFind();
        this.emojiTiles.callAll('spinEmoji');
        // all 3 colours in groups of 5.
        break;

      case 8:
        this.rotateBoard.stop();
        this.emojiTint(0x0a0aee); // blue
        this.emojiTiles.callAll('spinEmoji', null, false);
        break;

      case 10:
        this.emojiTint(0x000000); // black silouette 
        break;

      case 11:
        this.strobeBoard();
        break;

      case 12:
      //ACID
      //https://phaser.io/examples/v2/display/pixi-render-texture
        break;

      case 13:
        this.strobeBoard(false);
        this.rotateBoard.start();
        break;
      }
    }
  }

  gameOver() {
    this.game.state.start('GameOver', true, false, this.score.getScore());
  }

  setupScreenQuake() {
    // we need to add margin to the world, so the camera can move
    var margin = 50;
    // and set the world's bounds according to the given margin
    var x = -margin;
    var y = -margin;
    var w = this.world.width + margin * 2;
    var h = this.world.height + margin * 2;
    // it's not necessary to increase height, we do it to keep uniformity
    this.world.setBounds(x, y, w, h);
    
    // we make sure camera is at position (0,0)
    this.world.camera.position.set(0);
  }

  emojiSpin(start = true) {
    if(start) {
      this.emojiTiles.callAll('spinEmoji');
      this.sndRotate.play();
    } else {
      this.emojiTiles.callAll('spinEmoji', null, false);
    }

  }

  emojiTint(mode = this.tintmode) {
    this.emojiTiles.setAll('tint', mode);

    this.tintmode = mode;
  }

  // Callback used by tween onStart
  rotateBoardSound() {
    this.sndRotate.play();
  }

  strobeBoard(start = true) {
    if(start) {
      this.strobe.start();
    } else {
      this.strobe.pause();
    }
  }

  resize() {
    console.log('state world resize detected');
  }

  scaleinfo() {
    var s = 'Game size: ' + this.game.width + ' x ' + this.game.height + '\n';
    s = s.concat('Actual size: ' + this.scale.width + ' x ' + this.scale.height + '\n');
    s = s.concat('minWidth: ' + this.scale.minWidth + ' - minHeight: ' + this.scale.minHeight + '\n');
    s = s.concat('maxWidth: ' + this.scale.maxWidth + ' - maxHeight: ' + this.scale.maxHeight + '\n');
    s = s.concat('aspect ratio: ' + this.scale.aspectRatio + '\n');
    s = s.concat('parent is window: ' + this.scale.parentIsWindow + '\n');
    s = s.concat('bounds x: ' + this.scale.bounds.x + ' y: ' + this.scale.bounds.y + ' width:' + this.scale.bounds.width + ' height: ' + this.scale.bounds.height + '\n');
    this.info.text = s;
  }
}